import { useCallback, useState } from "react";
import { useLocation } from "react-router-dom";
import { Button } from "../../../components";
import style from "./home.nav.module.scss";

const HomeNav = ({ onClick = (e) => {} }: { onClick: (e: any) => void }) => {

  const [menu, toggleMenu] = useState(false);
  const location = useLocation();
  const { pathname } = location;
  const splitLocation = pathname.split("/");

  const onClickL = useCallback((e: any) => {
    onClick(e);
    toggleMenu(false);
  }, []);
 

  return (
    <div className={style.menuContainer}>
      <div onClick={() => toggleMenu((prev) => !prev)} className={[style.sShow, style.mobNav].join(' ')}>Menu</div>
      <ul className={`${style.sHidden} ${menu ? style.sShow : ''}`}>
        <li onClick={onClickL} data-url="/signin">
          Signin
        </li>
        <li onClick={onClickL} data-url="/view-plans">
          View plans
          {splitLocation[1] === 'view-plans' ? <div className={style.border}></div> : null}
        </li>
        <li>
          {" "}
          <Button
            className={style.freeTrialBtn}
            onClick={onClickL}
            data-url="free-trial"
          >
            Start a free trial
          </Button>
        </li>
      </ul>
    </div>
  );
};

export default HomeNav;
